body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #D4AF37;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search_field {
	border: 4px solid #D4AF37;
}
.sorting_menu
{
	margin-bottom: 6%;
	margin-left: 5%;
}

a {
		background-color: #FFFFFF;
}
.App {
  width: 100%;
  height: 100%;
  font-family: 'Love Ya Like A Sister', cursive;
  background-color: #D4AF37;
}

.container {
  background-image: -webkit-linear-gradient(left, black, grey);
  background-image: linear-gradient(to right, black, grey);;
  border: solid white 2px;
  text-align: left;
}


ul {
  list-style-type: none;
  text-align: left;

}

h1 {
  font-size: 4vw;
  text-align: center;
  margin-left: 4%;
  margin-top: 4%;
}

.row {
  margin-top: 10%;
}

.gradient_text {
  background-image: -webkit-gradient( linear, left top, right top, color-stop(0, #D4AF37), color-stop(0.5, #FFD700), color-stop(1, #FFA500));
  background-image: gradient( linear, left top, right top, color-stop(0, #D4AF37), color-stop(0.5, #FFD700), color-stop(1, #FFA500));
  color:transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

label, h4, h3 {
    background-image: -webkit-gradient( linear, left top, right top, color-stop(0, #D4AF37), color-stop(0.5, #FFD700), color-stop(1, #FFA500));
  background-image: gradient( linear, left top, right top, color-stop(0, #D4AF37), color-stop(0.5, #FFD700), color-stop(1, #FFA500));
  color:transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

